.App {
  flex-direction: column;
  display: flex;
  justify-content: center;
  padding: 1em;
}

.chart {
  border: 1px solid;
  padding: 5px;
  margin: 20px 0;
  width: 700px;
}

.filters {
  width: 250px;
}
.filters input {
  margin-bottom: 2px;
  padding: 5px;
  width: 100%;
}
.filterButtons {
  display: flex;
  justify-content: space-between;
}
.filterButtons button {
  background-color: rgba(40,44,52,.05);
  border: 1px solid #e0e0e0;
  color: #3080d0;
  cursor: pointer;
  padding: 8px 16px;
}
@media only screen and (max-width: 800px) {
  .chart {
    width: 100%;
  }
}
